<template>
  <main id="informat">
    <Header title="咨询"></Header>
    <div style="overflow: scroll; height: calc(100vh - 91px);" ref="wrapper">
      <mt-loadmore
        :top-method="loadTop"
        :bottom-method="loadBottom"
        :bottom-all-loaded="allLoaded"
        ref="loadmore"
      >
        <div slot="top" class="mint-loadmore-top">
          <span
            v-show="topStatus !== 'loading'"
            :class="{ 'rotate': topStatus === 'drop' }"
            style="color:#03a9f4"
          >下拉释放刷新</span>
          <span v-show="topStatus === 'loading'"></span>
        </div>
        <div slot="bottom" class="mint-loadmore-bottom">
          <div
            v-show="bottomStatus !== 'loading'&&!bottoming"
            :class="{ 'rotate': bottomStatus === 'drop' }"
            id="spinner" 
          >
            <mt-spinner
              type="fading-circle"
              color="#03a9f4"
              :class="{ 'rotate': bottomStatus === 'drop' }"
            ></mt-spinner>
          </div>
        </div>
        <mt-swipe :auto="5000" id="banner">
          <mt-swipe-item v-for="(item,index) in dataList" :key="index">
            <div>
              <img :src="item.coverImg" class="img" @click="check(item.id)">
              <span class="desc">{{item.digest}}</span>
            </div>
          </mt-swipe-item>
        </mt-swipe>
        <ul class="ifoList tal">
          <li v-for="(item,index) in dataList" :key="index" @click="check(item.id)">
            <div class="ifoPhoto">
              <img v-bind:src="item.coverImg" alt>
            </div>
            <div class="ifoMsg">
              <strong>{{item.title}}</strong>
              <p v-html="item.content"></p>
              <p class="ifoTime">{{item.createAt |datePipeYml}}</p>
            </div>
          </li>
        </ul>
        <p v-if="allLoaded" style="color:#03a9f4;margin-bottom:20px;text-align:center;">到底啦</p>
      </mt-loadmore>
    </div>
  </main>
</template>


<script>
import { Swipe, SwipeItem } from "mint-ui";
import "mint-ui/lib/style.css";
import Header from "@/components/Header.vue";
import { getNews } from "@/http/content/news.ts";
export default {
  data() {
    return {
      dataList: [],
      ifoList: [],
      ifoLists: [],
      param: {},
      params: {},
      total: 0,
      allLoaded: false,
      topStatus: '',
      bottomStatus: "",
      bottoming: false,
    };
  },
  created: function() {
      console.log(this.allLoaded)
    this.params.page = 1,
      (this.getIfo = function() {
        console.log(this.params)
        getNews(this.params).then(res => {
        console.log(res)
          let data = res.data.data;
          this.dataList = data.records;
          this.total = res.data.data.total;
        });
      });
    this.getIfo();
  },
  methods: {
    check(id) {
      this.$router.push({ 
        path: '/InformationDetails',
        query: {
            id: id,
        },
    });
    },
    //上啦下滑
    topChange(status) {
      this.topStatus = status;
    },
    bottomChange(status) {
      this.bottomStatus = status;
    },
    loadTop: function() {
      //组件提供的下拉触发方法
      //下拉加载
      this.ifoList = [];
      this.params.page = 1;
      this.getIfo();
      this.allLoaded = false; // 若数据已全部获取完毕
      this.$refs.loadmore.onTopLoaded(); // 固定方法，查询完要调用一次，用于重新定位
    },
    loadBottom: function() {
      if (this.params.page * this.size < this.total) {
        this.params.page++;
        this.getIfo();
      } else {
        this.bottoming = true; // 若数据已全部获取完毕
        this.allLoaded = true;
      }
      this.$refs.loadmore.onBottomLoaded();
    }
  }
};
</script>
<style lang="scss" scoped>
#informat {
  overflow: hidden;
  background: #eff4f8;
  min-height: calc(100vh - 125px);

  .bannner {
    margin: 7% 3%;

    img {
      width: 100%;
    }
  }
}
#informat li {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 3% 7% 3%;
  padding: 2%;
  border-radius: 5px;
  box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.37);
  background: #fff;

  .ifoPhoto {
    width: 40%;
    margin-right: 3%;

    img {
        width: 120px;
        height: 120px;
    }
  }
  .ifoMsg {
    width: 60%;
    p {
      padding: 4% 0;
      color: #aaa;
    }
    .ifoTime {
      padding: 0;
      text-align: right;
    }
  }
}

#banner {
  position: relative;
  height: 30vh;
  margin-bottom: 5%;
  div {
    height: 30vh;
    overflow: hidden;
    img {
      width: 100vw;
      height: 30vh;
    }
  }
  .desc {
    text-align: center;
    position: absolute;
    bottom: 0;
    z-index: 1;
    display: block;
    width: 100%;
    color: #fff;
    background: rgba(0, 0, 0, 0.5);
  }
  // .mint-swipe-indicators {
  //   bottom: 30px;
  // }
}
</style>